	},
	methods: {
		onClick(event) {
			wx.showToast({
				title: `点击标签 ${event.detail.name}`,
				icon: 'none'
			});
		},
		showPopup() {
			this.$data.show = true
		},
		onClose() {
			this.$data.show = false
		},
